import "./user.css";

export const createUser = () => {
  const columnTable = [
    "Name",
    "Read",
    "Update",
    "Create",
    "Delete",
    "Purge",
    "Read Notes",
    "Update Notes",
    "Select/Unselect All",
  ];

  const users = ["Mouy", "Tey", "Houy", "Noynoy", "Leng"];

  const userPermission = document.createElement("div");
  userPermission.classList.add("user-table");

  const table = document.createElement("table");

  const thead = document.createElement("thead");
  const headerRow = document.createElement("tr");

  columnTable.forEach((column) => {
    const th = document.createElement("th");
    th.textContent = column;
    headerRow.appendChild(th);
  });

  thead.appendChild(headerRow);

  const tbody = document.createElement("tbody");

  users.forEach((user) => {
    const row = document.createElement("tr");

    const nameCell = document.createElement("td");
    nameCell.textContent = user;
    row.appendChild(nameCell);

    for (let i = 1; i < columnTable.length; i++) {
      const switchCell = document.createElement("td");

      // Create the switch container
      const switchContainer = document.createElement("label");
      switchContainer.classList.add("switch");

      // Create the input element (the switch)
      const switchInput = document.createElement("input");
      switchInput.type = "checkbox";
      switchInput.name = `${columnTable[i]}-${user}`;
      switchInput.value = `${columnTable[i]}`;

      // Create the slider
      const slider = document.createElement("span");
      slider.classList.add("slider");

      // Append the input and slider to the switch container
      switchContainer.appendChild(switchInput);
      switchContainer.appendChild(slider);

      // Append the switch container to the table cell
      switchCell.appendChild(switchContainer);
      row.appendChild(switchCell);

      // Event listener to change background color when switch is checked
      switchInput.addEventListener("change", (event) => {
        if (event.target.checked) {
          row.style.backgroundColor = "lightgreen";
        } else {
          row.style.backgroundColor = "";
        }
      });
    }

    tbody.appendChild(row);
  });

  table.appendChild(thead);
  table.appendChild(tbody);
  userPermission.appendChild(table);

  document.body.appendChild(userPermission);

  return userPermission;
};
